<template>
  <div id="app">
    <Hello></Hello>
  </div>
</template>

<script>
import Hello from './components/form.vue'

export default {
  name: 'app',
  created(){
    fetch("http://www.localhost:3000/abc").then(function(data){
      return data.json()
    }).then(function(data){
      console.log(data);
    })
  },
  components: {
    Hello
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



